@import '../../style/mixins.scss';
.about {
    &__items {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        width: 1300px;
        margin: 0 auto;
    }
    &__item {
        width: 650px;
        height: 590px;
        img {
            display: block;
            width: 100%;
            height: 100%;
        }
        &_text {
            background-color: #C1BA15;
            padding: 50px 110px 60px 120px;
        }
    }
    &__title {
        font-weight: 400;
        font-size: 64px;
        line-height: 96px;
        text-align: center;
        color: #fff;
    }
    &__descr {
        @include raleway;
        font-weight: 400;
        font-size: 24px;
        line-height: 36px;
        margin-top: 60px;
        text-align: center;
        color: #fff;
    }
}

@media (max-width: 1400px) {
    .about {
        &__items {
            width: 1060px;
        }
        &__item {
            width: 530px;
            height: 470px;
            &_text {
                padding: 25px;
            }
        }
        &__descr {
            margin-top: 25px;
        }
    }
}

@media (max-width: 1200px) {
    .about {
        &__items {
            width: 850px;
        }
        &__item {
            width: 425px;
            height: 365px;
        }
        &__title {
            font-size: 52px;
            line-height: 50px;
        }
        &__descr {
            font-size: 20px;
            line-height: 28px;
        }
    }
}

@media (max-width: 991px) {
    .about {
        &__items {
            width: 728px;
        }
        &__item {
            width: 364px;
            height: 304px;
        }
        &__title {
            font-size: 48px;
            line-height: 50px;
        }
        &__descr {
            font-size: 18px;
            line-height: 24px;
            margin-top: 10px;
        }
    }
}

@media (max-width: 767px) {
    .about {
        &__items {
            width: 536px;
        }
        &__item {
            margin-top: 20px;
        }
    }
}

@media (max-width: 575px) {
    .about {
        &__items {
            width: 280px;
        }
        &__item {
            width: 280px;
            height: 280px;
        }
        &__title {
            font-size: 26px;
            line-height: 26px;
        }
        &__descr {
            font-size: 16px;
            line-height: 20px;
            margin-top: 10px;
        } 
    }
}