@import '../appCatalogNewBooks/appCatalogNewBooks.scss';

.promo-second {
    background: url('../../resources/img/second-bg.jpg') center center/cover no-repeat;
}

@media (max-width: 1400px) {
    
}

@media (max-width: 1200px) {
    .promo-second {
        &__title {
            width: 500px;
            font-size: 70px;
            line-height: 85px;
        }
    }
}

@media (max-width: 991px) {
    .promo-second {
        &__title {
            width: 400px;
            font-size: 50px;
            line-height: 65px;
        }
    }
}

@media (max-width: 767px) {
    .promo-second {
        &__title {
            width: 300px;
            font-size: 36px;
            line-height: 42px;
        }
    }
}

@media (max-width: 575px) {
    .promo-second {
        &__title {
            width: 200px;
            font-size: 32px;
            line-height: 38px;
        }
    }
}