.header {
    position: absolute;
    width: 100%;
    padding-top: 10px;
    &__arrow-bottom {
        position: absolute;
        top: 41px;
        left: 50%;
        transform: translateX(-50%);
        cursor: pointer;
        z-index: 5;
        &_hide {
            display: none;
        }
    }
    &__logo {
        position: absolute;
        z-index: 100;
    }
}

.menu {
    position: absolute;
    width: 420px;
    height: 302px;
    top: 40px;
    left: 50%;
    transform: translateX(-50%);
    overflow: hidden;
    z-index: 2;
    &__inner {
        position: absolute;
        top: -100%;
        width: 100%;
        height: 100%;
        background-color: #fff;
        display: flex;
        align-items: center;
        flex-direction: column;
        padding: 27px 0 54px 0;
        transition: 0.5s all;
        &_active {
            top: 0;
        }
    }
    &__list {
        li {
            display: block;
            font-weight: 400;
            font-size: 30px;
            line-height: 45px;
            margin-bottom: 56px;
            cursor: pointer;
            a {
                color: #000;
                text-decoration: none;
            }
        }
    }
    img {
        display: block;
        width: 64px;
        margin-bottom: 70px;
        cursor: pointer;
    }

}

@media (max-width: 1200px) {
    .header {
        min-height: 670px;
    }
}

@media (max-width: 991px) {
    .header {
        min-height: 500px;
        // &__logo {
        //     font-size: 20px;
        // }
    }
    .menu {
        width: 300px;
        height: 250px;
        &__list {
            li {
                margin-bottom: 20px;
            }
        }
        img {
            margin-bottom: 55px;
        }
    }
}

@media (max-width: 767px) {
    .header {
        min-height: 400px;
        // &__logo {
        //     font-size: 15px;
        //     line-height: 30px;
        // }
        img {
            width: 50px;
        }
    }
    .menu {
        width: 250px;
        height: 200px;
        &__list {
            li {
                margin-bottom: 5px;
                font-size: 27px;
            }
        }
        img {
            width: 50px;
            margin-bottom: 30px;
        }
    }
}

@media (max-width: 575px) {
    .header {
        min-height: 330px;
        // &__logo {
        //     font-size: 12px;
        //     line-height: 20px;
        // }
        img {
            width: 40px;
        }
    }
    .menu {
        width: 200px;
        height: 150px;
        &__list {
            li {
                margin-bottom: 0px;
                font-size: 22px;
            }
        }
        &__inner {
            padding: 15px 0;
        }
        img {
            width: 40px;
            margin-bottom: 20px;
        }
    }
}