.promo {
    position: relative;
    background: url('../../resources/img/main-bg.jpg') center center/cover no-repeat;
    min-height: 820px;
    padding-top: 10px;
    &__title {
        width: 860px;
        margin-top: 120px;
        font-weight: 400;
        font-size: 80px;
        line-height: 120px;
        text-transform: uppercase;
        color: #ffffff;
    }
}

@media (max-width: 1200px) {
    .promo {
        min-height: 670px;
        &__title {
            width: 790px;
            line-height: 100px;
            font-size: 70px;
        }
    }
}

@media (max-width: 991px) {
    .promo {
        min-height: 500px;
        &__title {
            width: 400px;
            line-height: 80px;
            font-size: 50px;
        }
    }
}

@media (max-width: 767px) {
    .promo {
        min-height: 400px;
        &__title {
            width: 330px;
            line-height: 50px;
            font-size: 40px;
        }
    }
}

@media (max-width: 575px) {
    .promo {
        min-height: 330px;
        &__title {
            width: 210px;
            line-height: 35px;
            font-size: 25px;
        }
    }
}
