@import '../../style/mixins.scss';

.form {
    min-height: 878px;
    padding: 143px 0 207px 0;
    &__title {
        max-width: 400px;
        font-weight: 400;
        font-size: 42px;
        line-height: 54px;
        margin: 0 auto;
        text-align: center;
        color: #000;
    }
    form {
        display: flex;
        align-items: end;
        margin-top: 150px;
        padding-left: 100px;
    }
    &__inputs {
        display: flex;
        flex-direction: column;
        width: 750px;
        input {
            display: flex;
            align-items: center;
            width: 100%;
            height: 60px;
            padding: 0 43px;
            margin-bottom: 42px;
            border: 3px solid #000000;
            @include montserrat;
            font-weight: 500;
            font-size: 24px;
            line-height: 24px;
            outline: none;
            &:last-child {
                margin-bottom: 0px;
            }
        }
    }
    &__success-message {
        display: none;
        @include raleway;
        font-weight: 400;
        font-size: 24px;
        line-height: 36px;
        text-align: center;
        margin: 0 0 100px 20px;
        transition: 0.5s all;
        &_show {
            display: block;
        }
    }
    button {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 310px;
        height: 60px;
        margin-left: 63px;
        border: 3px solid #000000;
        background-color: #fff;
        text-transform: uppercase;
        font-weight: 400;
        font-size: 30px;
        line-height: 45px;
        outline: none;
        cursor: pointer;
        &:focus {
            outline: none;
        }
    }
}

@media (max-width: 1400px) {
    .form {
        &__inputs {
            width: 580px;
        }
    }
}

@media (max-width: 1200px) {
    .form {
        min-height: 580px;
        padding: 70px 0 100px 0;
        &__inputs {
            width: 480px;
            input {
                height: 50px;
                font-size: 22px;
            }
        }
        form {
            margin-top: 70px;
        }
        button {
            width: 220px;
            height: 50px;
            font-size: 26px;
        }
        &__success-message {
            margin: 0 0 50px 20px;
            font-size: 22px;
        }
    }
}

@media (max-width: 991px) {
    .form {
        min-height: 480px;
        padding: 60px 0 60px 0;
        &__title {
            font-size: 36px;
            line-height: 40px;
        }
        &__inputs {
            width: 370px;
            input {
                height: 40px;
                font-size: 20px;
                padding-left: 15px;
                margin-bottom: 30px;
                border: 2px solid #000000;
            }
        }
        form {
            padding-left: 30px;
        }
        button {
            width: 180px;
            height: 40px;
            font-size: 20px;
            border: 2px solid #000000;
        }
        &__success-message {
            margin: 0 0 50px 20px;
            font-size: 18px;
        }
    }
}

@media (max-width: 767px) {
    .form {
        min-height: 480px;
        padding: 60px 0 60px 0;
        &__title {
            font-size: 30px;
            line-height: 35px;
        }
        &__inputs {
            width: 370px;
            input {
                height: 40px;
                font-size: 20px;
                padding-left: 15px;
                margin-bottom: 30px;
                border: 2px solid #000000;
            }
        }
        form {
            flex-direction: column;
            align-items: center;
            padding-left: 0;
            margin-top: 40px;
        }
        button {
            margin: 0 auto;
            margin-top: 30px;
        }
        &__success-message {
            margin: 20px 0 0 0;
            line-height: 28px;
        }
    }
}

@media (max-width: 575px) {
    .form {
        min-height: 370px;
        padding: 40px 0 40px 0;
        &__title {
            font-size: 26px;
            line-height: 30px;
        }
        &__inputs {
            width: 250px;
            input {
                height: 30px;
                font-size: 16px;
                padding-left: 10px;
                margin-bottom: 20px;
                border: 2px solid #000000;
            }
        }
        form {
            flex-direction: column;
            align-items: center;
            padding-left: 0;
            margin-top: 40px;
        }
        button {
            height: 30px;
            font-size: 18px;
            margin-top: 20px;
        }
        &__success-message {
            font-size: 14px;
            line-height: 22px;
        }
    }
}