@import './mixins.scss';
@import url('https://fonts.googleapis.com/css2?family=Forum&family=Montserrat:wght@500&family=Raleway&display=swap');
@import url(https://allfont.ru/index.php/allfont.css?fonts=archeologicaps);

* {
    font-family: 'Forum', cursive;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

ul,
li,
dl {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

.app {
    position: relative;
}

.container {
    max-width: 1490px;
    margin: 0 auto;
    padding: 0 70px;
}

.logo {
    @include arch;
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    text-transform: uppercase;
    color: #FFFFFF;
}

.message {
    @include raleway;
    font-weight: 400;
    font-size: 24px;
    line-height: 36px;
    text-align: center;
    margin: 0 auto;
}

@media (max-width: 991px) {
    .container {
        padding: 0 20px;
    }
    .logo {
        font-size: 20px;
    }
}

@media (max-width: 767px) {
    .logo {
        font-size: 15px;
        line-height: 30px;
    }
}

@media (max-width: 575px) {
    .logo {
        font-size: 12px;
        line-height: 20px;
    }
}