@import '../../style/mixins.scss';

.new {
    min-height: 1220px;
    padding: 140px 0 260px 0;
    &__title {
        font-weight: 400;
        font-size: 64px;
        line-height: 96px;
        text-align: center;
        text-transform: uppercase;
    }
    &-items {
        width: 1300px;
        margin: 0 auto;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        &__item {
            position: relative;
            width: 420px;
            min-height: 650px;
            border: 3px solid #000000;
            margin-top: 70px;
            padding: 25px 25px 100px 25px;
        }
        img {
            display: block;
            width: 200px;
            margin: 0 auto;
        }
        &__name {
            @include raleway;
            width: 320px;
            font-weight: 400;
            font-size: 24px;
            line-height: 36px;
            margin: 0 auto;
            margin-top: 29px;
            text-align: center;
            color: #000;
        }
        &__genre {
            @include raleway;
            width: 320px;
            font-weight: 400;
            font-size: 18px;
            line-height: 100px;
            text-align: center;
            margin: 0 auto;
            color: #000000;
        }
        &__price {
            @include raleway;
            position: absolute;
            bottom: 25px;
            left: 25px;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 175px;
            height: 75px;
            font-weight: 400;
            font-size: 30px;
            line-height: 45px;
        }
    }
}

@media (max-width: 1400px) {
    .new {
        &__title {
            font-size: 60px;
        }
        &-items {
            width: 1050px;
            &__item {
                width: 330px;
                min-height: 600px;
            }
            img {
                width: 180px;
            }
            &__name {
                width: 280px;
                font-size: 22px;
            }
            &__genre {
                width: 280px;
            }
            &__price {
                font-size: 28px;
            }
        }
    }
}

@media (max-width: 1200px) {
    .new {
        padding: 30px 0;
        &__title {
            font-size: 56px;
        }
        &-items {
            width: 780px;
            &__item {
                width: 330px;
                min-height: 570px;
                margin-top: 30px;
            }
            img {
                width: 180px;
            }
            &__name {
                width: 280px;
                font-size: 22px;
            }
            &__genre {
                width: 280px;
                line-height: 80px;
            }
            &__price {
                font-size: 28px;
            }
        }
    }
}

@media (max-width: 991px) {
    .new {
        &__title {
            font-size: 56px;
        }
        &-items {
            width: 650px;
            &__item {
                padding-bottom: 15px;
                width: 300px;
                min-height: 500px;
            }
            img {
                width: 160px;
            }
            &__name {
                width: 230px;
                font-size: 20px;
            }
            &__genre {
                width: 230px;
                line-height: 18px;
                margin-top: 35px;
            }
            &__price {
                font-size: 24px;
                bottom: 0;
                left: 0;
            }
        }
    }
}

@media (max-width: 767px) {
    .new {
        &__title {
            font-size: 52px;
        }
        &-items {
            width: 300px;
            justify-content: center;
        }
    }
}

@media (max-width: 575px) {
    .new {
        &__title {
            font-size: 36px;
            line-height: 50px;
        }
        &-items {
            width: 280px;
            &__item {
                width: 280px;
                min-height: 380px;
            }
            img {
                width: 120px;
            }
            &__name {
                font-size: 18px;
                margin-top: 20px;
                line-height: 20px;
            }
            &__genre {
                font-size: 16px;
                margin-top: 20px;
            }
            &__price {
                font-size: 20px;
                width: 120px;
                height: 70px;
            }
        }
    }
}