@import '../../style/mixins.scss';

.footer {
    background-color: #1596C1;
    &__blocks {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 131px;
    }
    &__phone {
        @include raleway;
        font-weight: 400;
        font-size: 24px;
        line-height: 36px;
        a {
            color: white;
            text-decoration: none;
        }
    }
    &__menu {
        display: flex;
        li {
            font-weight: 400;
            font-size: 24px;
            line-height: 36px;
            margin: 0 50px;
            color: #FFFFFF;
            cursor: pointer;
            a {
                color: #fff;
                text-decoration: none;
            }
        }
    }
}

@media (max-width: 1200px) {
    .footer {
        &__menu {
            li {
                margin: 0 15px;
            }
        }
        &__phone {
            font-size: 22px;
        }
    }
}

@media (max-width: 767px) {
    .footer {
        &__menu {
            flex-direction: column;
            li {
                margin: 0 10px 0 0;
                font-size: 22px;
            }
        }
    }
}

@media (max-width: 575px) {
    .footer {
        &__blocks {
            flex-wrap: wrap;
            justify-content: center;
        }
        &__menu {
            flex-direction: row;
            li {
                margin: 10px;
                font-size: 22px;
            }
        }
        &__phone {
            margin-left: 40px;
        }
    }
}